	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/02/crh0218.jpg\" height=\"263\" width=\"350\" border=\"0\" alt=\"crh0218.jpg: \" /></p>\n<p><img src=\"http://shanson.editthispage.com/picture$156\" align=\"right\" />We were finally able to photograph a big smile from Christopher. Mama's trick was to take his current favorite toy (a garish Beanie Baby named \"Spangle\") and dangle it next to the camera.</p>\n<p><a href=\"http://albums.photopoint.com/j/AlbumIndex?u=185392&a=1898060\">Week 8 Photos</a> at PhotoPoint </p>\n<p>An open and shut case of bednapping at <a href=\"http://familieberg.editthispage.com/2000/02/15\">Familie Berg</a>: \"Datt Bett is fott\"! The text is in German, but the pictures are clear enough. <i>Good thing Moritz wasn't inside!</i></p>\n<p><a href=\"http://2020hindsight.editthispage.com/discuss/msgReader$253\">2020 Hindsight</a>: \"I want to take my PowerPC 8100 and throw it out the window\". It turns out she <a href=\"http://2020hindsight.editthispage.com/2000/02/17\">didn't</a>, it was only the CD-ROM that was flakey. An 8100 was my true friend and companion for nearly two years at <a href=\"http://www.int-res.com\">Inter-Research</a>.</p>","fields":{"title":"Friday","date":"18 Feb 2000","url":"https://papascott.de/2000-02-18-friday/"}}},"pathContext":{"slug":"/2000-02-18-friday/","prev":{"fields":{"title":"Scripting News: \"Manila Express? is","date":"17 Feb 2000","slug":"/2000-02-17-scripting-news-manila-express-is/"}},"next":{"fields":{"title":"Saturday","date":"19 Feb 2000","slug":"/2000-02-19-saturday/"}}}}

